.bayes-spotlight {
    @include media('<=medium') {
        .bayes-full-width-container__wrapper {
            max-width: initial;
            margin-left: initial;
            margin-right: initial;
            padding-left: initial;
            padding-right: initial;
            width: 100%;
        }
    }

    @include media('>medium') {
        position: relative;

        &:before {
            content: '';
            width: 100%;
            height: calc(100% + 5rem);
            background-color: $home-spotlight;
            position: absolute;
            left: -50%;
            top: -5rem;
            z-index: 2;
        }
    }

    .bayes-spotlight__twocards {
        background-color: $home-spotlight;
        padding: 2rem 2rem 2rem 1rem;
        position: relative;

        @include media('>large') {
            padding-right: 8rem;
        }

        @include media('>medium') {
            z-index: 3;

            &:before {
                content: '';
                height: 5rem;
                width: 100%;
                background-color: $home-spotlight;
                position: absolute;
                top: -5rem;
                left: 0;
            }
        }

        @include media('<=medium') {
            padding: 2rem;
        }
    }
}

.bayes-spotlight__cards {
    display: flex;
    flex-direction: column;

    li {
        display: block;
        margin-bottom: $measure-medium;
    }

    a {
        @include anchor-reset();
        @include border-radius(0.25rem);
        display: flex;
        justify-content: space-between;
        background-color: $color-white;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
        padding: 2rem;

        &:hover {
            background-color: $color-white;
            transform: scale(1.05);
        }

        &:focus {
            background-color: $color-white;
            outline: $border-width-outline $border-style-default
                $color-spotlight-focus;
            outline-offset: 3px;
        }

        &:active {
            background-color: $color-white;
            transform: scale(1.05);
            outline: $border-width-outline $border-style-alternate
                $color-spotlight-focus;
            outline-offset: 3px;
        }

        .bayes-spotlight__cards__wrapper {
            display: flex;
            flex-direction: column;
        }

        .bayes-spotlight__cards--title {
            font-size: 1.25rem;
            font-weight: bold;
            line-height: 1.3;
        }

        .bayes-spotlight__cards--icon {
            display: flex;
            flex-direction: column;
            justify-content: center;
            font-size: 1.25rem;
            //font-weight: normal;
        }
    }
}

.bayes-spotlight__feature {
    padding: 2rem;

    h2 {
        line-height: 2.2rem;
        margin-bottom: initial;
    }

    @include media('>large') {
        padding: 4rem 8rem;
    }
}
